import { Injectable, SkipSelf } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { GlobalVariable } from '../server'; 

@Injectable({
  providedIn: 'root'
})
export class ForgotPasswordService {
  private url = GlobalVariable.BASE_API_URL;

  constructor(private http: HttpClient) { }

  public checkEmailExist(email_address: string){
    console.log(email_address)
    return this.http.post<any>(this.url.concat('api/user/check-user-exist'),  {
        email_address: email_address,
        withCredentials: true
    }).pipe(
      catchError(this.handleError)
    );
  }

  public forgotPassword(email_address: string){
    return this.http.post<any>(this.url.concat('api/user/forgot-password'),  {
        email_address: email_address,
        withCredentials: true
    }).pipe(
      catchError(this.handleError)
    );
  }

  public getUserEmailByCode(code: string){
    return this.http.get<any>(this.url.concat('api/user/get-user-email-by-code/'+code, ),  {
      withCredentials: true
    }).pipe(
      catchError(this.handleError)
    );
  }

  public passwordReset(email: string, password: string) {
    return this.http.post<any>(this.url.concat('api/user/password-reset'),  {
        email: email,
        password: password
     })
     .pipe(
      catchError(this.handleError)
      );
    }

  private handleError(err: HttpErrorResponse) {
    return 'false';
  }

  // private handleError(err: HttpErrorResponse) {
  //   let errorMessage = '';
  //   if (err.error instanceof ErrorEvent) {
  //     errorMessage = `An error occurred: ${err.error.message}`;
  //   } else {
  //     errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;
  //   }
  //   console.error(errorMessage);
  //   return throwError(errorMessage);
  // }
}
