import { Component, OnInit } from '@angular/core';
import { TransationsService} from '../../../services/transactions.service';
import{ Subscription} from 'rxjs';

@Component({
  selector: 'app-passenger-transaction',
  templateUrl: './passenger-transaction.page.html',
  styleUrls: ['./passenger-transaction.page.scss'],
})
export class PassengerTransactionPage implements OnInit {

  public transaction_subscription: Subscription= new Subscription();
  constructor(
    private transactionService: TransationsService) { }

  ngOnInit() {
    console.log('hey');
    this.getTransactions();
  }

  
  public transactions: [];

  getTransactions(){
    this.transaction_subscription.add(
      this.transactionService.get_transactions(999999).subscribe(
        result => {
          console.log(result);
          if(result.result.length>0){
            this.transactions = result.result;
            console.log(this.transactions); 
          }
          else{
            this.transactions= []; 
          }
        },
        error => { 
        }
      )
    );
  }

}
